








































import { Component, Vue } from "vue-property-decorator";
import { mdiMapSearch, mdiFaceProfile } from "@mdi/js";

@Component
class BottomNav extends Vue {
  bottomNav = "wallet";

  bottomNavItems = [
    {
      text: "Me",
      icon: "$vuetify.icon.iconNavMe",
      value: "me",
    },
    {
      text: "Search",
      icon: mdiMapSearch,
      value: "search",
    },
    {
      text: "Profile",
      icon: mdiFaceProfile,
      value: "profile",
    },
  ];

  handleChange(nav) {
    this.bottomNav = nav.value;
  }
}
export default BottomNav;
